<template>
  <Container :title="title">
    <v-form @submit="submit">
      <template slot="base">
        <group :label="lang.key" type="input" v-model="config.key" />
        <group :label="lang.value" type="input" v-model="config.value" />
        <group :label="lang.description" type="input" v-model="config.description" />
        <group :label="lang.type" type="select" :options="type_options" v-model="config.type" />
      </template>
    </v-form>
  </Container>
</template>

<script>
  import {Container,VForm,Group} from "@/components"
  export default {
    components: { Group, VForm,  Container },
    data(){
      return {
        title:this.$t('config.editTitle'),
        lang:this.$t('config')
      }
    },
    computed:{
      type_options(){
        return this.$store.state.config.type_options
      },
      config(){
        return this.$store.state.config.config
      }
    },
    created(){
      const mid = this.$route.query['id']
      this.$store.dispatch('config/fetchConfig',mid)
    },
    methods:{
      submit(){
        this.$store.dispatch('config/editConfig').then( resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
          }
        })
      }
    }
  }
</script>



